export default {
  url: '/icebergChart',
  mock: {
    'code': 1,
    'msg': 'success',
    'result|4-6':  [
      {
        'name|+1': ['3月', '4月', '5月', '6月', '7月', '8月', '9月'],
        'value|100-1000': 1,
        'ratio|1-100':1
      }

    ]
  }
}



