import echarts from 'echarts'
import _ from 'lodash'

export default class BreakPie {
  defaultSetting() {
    return{
      width: 500,
      height: 500,
      rich: {
        white: {
          color: '#ddd',
          align: 'center',
          padding: [5, 0]
        }
      },
      placeHolderStyle: {
        normal: {
          label: {
            show: false
          },
          labelLine: {
            show: false
          },
          color: 'rgba(0, 0, 0, 0)',
          borderColor: 'rgba(0, 0, 0, 0)',
          borderWidth: 0
        }
      },
      tooltip: {
        show: true
      },
      legend: {
        show: false
      },
      toolbox: {
        show: false
      },
      seriesStyle: {
        name: '',
        type: 'pie',
        clockWise: false,
        radius: [160, 165],
        hoverAnimation: false,
        itemStyle: {
          normal: {
            label: {
              show: true,
              position: 'outside',
              color: '#ddd'
              
            },
            labelLine: {
              show: false
            }
          }
        }
      },
      itemStyle: {
        normal: {
          borderWidth: 5,
          shadowBlur: 30,
          borderColor: new echarts.graphic.LinearGradient(0, 0, 1, 1, [{
            offset: 0,
            color: '#7777eb'
          }, {
            offset: 1,
            color: '#70ffac'
          }]),
          shadowColor: 'rgba(142, 152, 241, 0.6)'
        }
      }
    }
  }
  
  /**
   * Creates an instance of donut3d
   * @param {string} selector 容器元素选择器
   * @param {object} opt 图表组件配置项
   */
  constructor(selector, opt) {
    // 获取配置项
    const defaultSetting = this.defaultSetting()
    this.config = _.merge({}, defaultSetting, opt)    
    let div = document.querySelector(selector)
    const { width, height } = this.config
    div.setAttribute('style', `width: ${width}px; height: ${height}px`)
    this.myChart = echarts.init(document.querySelector(selector))
    this.option = this.config
    this.formatter = (params, data) => {
      let percent = 0
      let total = 0
      for (let j = 0; j < data.length; j++) {
        total += data[j].value
      }
      percent = (params.value / total * 100).toFixed(0)
      if(params.name !== '') {
        return `${params.name }\n{white|占比${ percent }%}`
      }
      return ''
    }
  }
  
  /**
   *  渲染
   *  @example: [
   *    {
   *     'name': '@cname', // 名称
   *     'value|10-100': 1
   *    }
   *  ]
   *  @param {array} data 渲染组件需要的数据项
   *  @return   {void}
   */
  render(data) {
    let scaleData = data
    var dataset = []
    for (var i = 0; i < scaleData.length; i++) {
      dataset.push({
        value: scaleData[i].value,
        name: scaleData[i].name,
        itemStyle: this.config.itemStyle
      }, {
        value: 4,
        name: '',
        itemStyle: this.config.placeHolderStyle
      })
    }
    var seriesObj = [{
      ...this.config.seriesStyle,
      itemStyle: {
        normal: {
          label: {
            formatter: (params) =>{
              return this.formatter(params, scaleData)
            },
            rich: this.config.rich
          }
        }
      },
      data: dataset
    }]
    this.option.series = seriesObj
    this.myChart.setOption(this.option)
  }
}




