import echarts from 'echarts'
import _ from 'lodash'
import symbolBg from './images/symbol.png'
import symbol from './images/symbol-bg.png' 
export default class PictorialBar {
  defaultSetting() {
    return{
      width: 700,
      height: 400,
      option: {
        'grid': {
          'left': '20%',
          'top': '10%',
          'bottom': 10
        },
        'tooltip': {
          'trigger': 'item',
          'textStyle': {
            'fontSize': 12
          },
          'formatter': '{b0}:{c0}'
        },
        'xAxis': {
          'max': 300,
          'splitLine': {
            'show': false
          },
          'axisLine': {
            'show': false
          },
          'axisLabel': {
            'show': false
          },
          'axisTick': {
            'show': false
          }
        },
        'yAxis': [
          {
            'type': 'category',
            'inverse': false,
            'data': [
              '山西',
              '四川',
              '西藏',
              '北京',
              '上海',
              '内蒙古',
              '云南',
              '黑龙江',
              '广东',
              '福建'
            ],
            'axisLine': {
              'show': false,
              'lineStyle': {
                'color': '#fff'
              }
            },
            'axisTick': {
              'show': false
            },
            'axisLabel': {
              'margin': -4,
              'textStyle': {
                'color': '#fff',
                'fontSize': 16.25
              }
            }
          }
         
        ],
        'series': [
          {
            'type': 'pictorialBar',
            'symbol': 'image://data:image/png;base64,',
            'symbolRepeat': 'fixed',
            'symbolMargin': '5%',
            'symbolClip': true,
            'symbolSize': 22.5,
            'symbolPosition': 'start',
            'symbolOffset': [
              20,
              0
            ],
            'symbolBoundingData': 300,
            'data': [
              13,
              42,
              67,
              81,
              86,
              94,
              166,
              220,
              249,
              262
            ],
            'z': 10
          },
          {
            'type': 'pictorialBar',
            'itemStyle': {
              'normal': {
                'opacity': 0.3
              }
            },
            'label': {
              'normal': {
                'show': false
              }
            },
            'animationDuration': 0,
            'symbolRepeat': 'fixed',
            'symbolMargin': '5%',
            'symbol': 'image://data:image/png;base64,',
            'symbolSize': 22.5,
            'symbolBoundingData': 300,
            'symbolPosition': 'start',
            'symbolOffset': [
              20,
              0
            ],
            'data': [
              13,
              42,
              67,
              81,
              86,
              94,
              166,
              220,
              249,
              262
            ],
            'z': 5
          }
        ]
      }
    }
 
  }
  
  /**
   * Creates an instance of donut3d
   * @param {string} selector 容器元素选择器
   * @param {object} opt 图表组件配置项
   */
  constructor(selector, opt) {
    // 获取配置项
    const defaultSetting = this.defaultSetting()
    this.config = _.merge({}, defaultSetting, opt)    
    let div = document.querySelector(selector)
    const { width, height } = this.config
    div.setAttribute('style', `width: ${width}px; height: ${height}px`)
    this.myChart = echarts.init(document.querySelector(selector))
    this.option = this.config.option
  }
  
  /**
   *  渲染
   *  @example: [
   *    {
   *     'name': '@cname', // 名称
   *     'value|10-100': 1
   *    }
   *  ]
   *  @param {array} data 渲染组件需要的数据项
   *  @return   {void}
   */
  render() {
    this.myChart.setOption(this.option)
  }
}




