import d3 from 'd3'
import _ from 'lodash'
import { randomString } from '../../util/util'
import { filters } from '../../util/filters/index'

export default class Force {
  /**
   * 初始化默认配置
   * @param  {Object} option 用户定义配置项
   * @return {void}   void
   */
  initConfig(option) {
    // 配置主题栅格布局信息
    let svg = _.merge({
      width: 700,
      height: 400
    }, option.grid)

    let padding = {
      left: svg.width * 0.1,
      right: svg.width * 0.1,
      top: svg.height * 0.15,
      bottom: svg.height * 0.1
    }

    this.grid = _.merge({
      width: svg.width,
      height: svg.height,
      left: padding.left,
      right: padding.right,
      top: padding.top,
      bottom: padding.bottom
    }, option.grid)

    // 配置力导向图相关信息
    this.force = _.merge({
      charge: -400,
      draggable: true,

      // 连线的样式
      links: {
        distance: 50,
        stroke: '#FFF',
        strokeWidth: 2,
        strokeDasharray: null
      },

      // 节点的样式
      groups: {
        radius: 15,

        fill: 'rgba(241, 92, 128, 0.84)',
        stroke: 'rgba(241, 92, 128, 1)',
        strokeWidth: 1,

        fills: [
          '#f7a35c', 
          null, null, null, null, null, null, null, null, null, null, 
          '#90ed7d',
          null, null, null, null, null, null, null, null, null, null,
          '#8085e8'
        ],
        strokes: [
          '#f7a35c', 
          null, null, null, null, null, null, null, null, null, null, 
          '#90ed7d',
          null, null, null, null, null, null, null, null, null, null,
          '#8085e8'
        ]
      },  
      
      // 文字样式
      texts: {
        show: true,
        fill: '#000',
        fontSize: 12        
      }
    }, option.force)
  }

  /**
   * 初始化容器
   * @param  {String} selector 选择器
   * @return {void}   void
   */
  initGroup(selector) {
    let { width, height } = this.grid

    // 设置外层容器样式
    d3.select(selector).style({
      position: 'relative',
      width: `${width}px`,
      height: `${height}px`
    })   

    // 初始化 svg 容器
    this.svgid = `svg${randomString(10)}`
    let svgG = d3.select(selector)
      .append('svg')
      .attr('id', this.svgid)
      .attr('width', width)
      .attr('height', height)
    this.svgG = svgG    
    
    // 初始化连线容器
    this.linesG = svgG.append('g')
      .classed('lines', true)

    // 初始化节点容器
    this.groupsG = svgG.append('g')
      .classed('groups', true)
  }

  /**
   * 实例化
   * @param {String} selector 选择器
   * @param {Object} option   配置项
   */
  constructor(selector, option = {}) {
    this.initConfig(option)
    this.initGroup(selector)
  }

  /**
   * 创建比例尺
   * @param  {Array} series 数据集
   * @return {void}  void
   */
  createScale(series) {
    let { radius } = this.force.groups
    let arr = series.nodes.map(d => d.value)
    let [min, max] = [d3.min(arr), d3.max(arr)]

    return d3.scale.linear()
      .domain([min, max])
      .range([radius / 2, radius])
  }

  /**
   * 绘制图表
   * @param  {Array} series 数据集
   * @return {void}  void
   */
  drawChart(series) {
    // 创建比例尺
    this.scale = this.createScale(series)

    // 转换数据
    let { width, height } = this.grid
    let { charge, links, draggable } = this.force
    let { distance } = links

    this.forceLayout = d3.layout.force()
      .nodes(series.nodes)
      .links(series.links)
      .linkDistance(distance)
      .charge(charge)
      .size([width, height])

    this.forceLayout.start()

    // 绘制
    let lines = this.drawLinks(series.links)
    let groups = this.drawGroups(series.nodes)
    let texts = this.drawTexts(series.nodes)

    // 动态设置偏移量
    this.forceLayout.on('tick', () => {
      // 设置连线
      lines
        .attr('x1', d => d.source.x)
        .attr('y1', d => d.source.y)
        .attr('x2', d => d.target.x)
        .attr('y2', d => d.target.y)

      // 设置节点
      groups
        .attr('cx', d => d.x)
        .attr('cy', d => d.y)

      if (draggable) {
        groups.call(this.forceLayout.drag)
      }

      // 设置文字
      texts
        .attr('x', d => d.x)
        .attr('y', d => d.y)
    })
  }

  /**
   * 绘制文字
   * @param  {Array}  nodes nodes 数据集
   * @return {Object} 返回节点的选择集对象
   */
  drawTexts(nodes) {
    let { show, fill, fontSize } = this.force.texts

    return this.groupsG.selectAll('text')
      .data(nodes)
      .enter()
      .append('text')
      .text(d => show ? d.name ? d.name : null : null)
      .attr('fill', fill)
      .attr('font-size', fontSize)
      .attr('text-anchor', 'middle')
      .attr('dy', '.35em')
  }  

  /**
   * 绘制节点
   * @param  {Array}  nodes nodes 数据集
   * @return {Object} 返回节点的选择集对象
   */
  drawGroups(nodes) {
    let { radius } = this.force
    let { fill, stroke, fills, strokes, strokeWidth } = this.force.groups

    return this.groupsG.selectAll('circle')
      .data(nodes)
      .enter()
      .append('circle')
      .attr('cursor', 'pointer')
      .attr('r', d => d.value ? this.scale(d.value) : radius)
      .attr('fill', d => {
        if (fills.length > 0) {
          if (!fills[d.index]) {
            return fill
          }
          return filters(this.svgG, fills[d.index])
        }
        return fill
      })
      .attr('stroke', d => {
        if (strokes.length > 0) {
          if (!strokes[d.index]) {
            return stroke
          }
          return filters(this.svgG, strokes[d.index])
        }
        return stroke
      })
      .attr('stroke-width', strokeWidth)
  }

  /**
   * 
   * @param  {Array} links links 数据集
   * @return {void}  返回连线的选择集对象
   */
  drawLinks(links) {
    let { stroke, strokeWidth, strokeDasharray } = this.force.links

    return this.linesG.selectAll('line')
      .data(links)
      .enter()
      .append('line')
      .attr('stroke', stroke)
      .attr('stroke-width', strokeWidth)
      .attr('stroke-dasharray', strokeDasharray)
  }

  /**
   * 渲染图表
   * @param  {Object} series 数据
   * @return {void}  void
   */
  render(series) {
    this.drawChart(series)
  }
}



