const month = ['一月', '二月', '三月', '四月', '五月']

export default {
  'url': '/columnBar',
  'mock': {
    'code': 1,
    'msg': 'success',
    'result': [
      {
        'name': '重庆市区',
        'data|5': [
          {
            'name|+1': month,
            'value|10-100': 10
          }
        ]
      },
      {
        'name': '成都熊猫基地',
        'data|5': [
          {
            'name|+1': month,
            'value|10-100': 10
          }
        ]
      },
      {
        'name': '四川省',
        'data|5': [
          {
            'name|+1': month,
            'value|10-100': 10
          }
        ]
      },
      {
        'name': '北京',
        'type': 'line',
        'data|5': [
          {
            'name|+1': month,
            'value|10-100': 10
          }
        ]
      }
    ]
  }
}



