const month = ['一月', '二月', '三月', '四月', '五月']

export default {
  'url': '/radar2',
  'mock': {
    'code': 1,
    'msg': 'success',
    'result|3': [
      {
        'name|+1': ['重庆市区', '成都熊猫基地', '四川省'],
        'data|5': [
          {
            'name|+1': month,
            'value|50-100': 10
          }
        ] 
      }
    ]
  }
}



